
import { defineComponent, PropType } from 'vue'

import type { VideoInfo } from '@/api/type'
import VideoItem from '@/components/VideoItem.vue'

export default defineComponent({
  name: 'VideoList',
  components: {
    VideoItem
  },
  props: {
    videos: {
      type: Array as PropType<VideoInfo['data']>,
      required: true
    }
  }
})
